					"content": "<p>Refer to the <a href=\"https://etcher.io\">downloads page</a> for the latest pre-made\ninstallers for all supported operating systems.</p>\n<h4>Debian and Ubuntu based Package Repository (GNU/Linux x86/x64)</h4>\n<ol>\n<li>\n<p>Save the following as <code>/etc/apt/sources.list.d/etcher.list</code>:</p>\n<pre><code>deb https://dl.bintray.com/resin-io/debian stable etcher\n</code></pre>\n</li>\n<li>\n<p>Trust Bintray.com's GPG key:</p>\n<pre><code class=\"undefinedsh\">sudo apt-key adv --keyserver hkp://pgp.mit.edu:80 --recv-keys 379CE192D401AB61\n</code></pre>\n</li>\n<li>\n<p>Update and install:</p>\n<pre><code class=\"undefinedsh\">sudo apt-get update\nsudo apt-get install etcher-electron\n</code></pre>\n</li>\n</ol>\n<h5>Uninstall</h5>\n<pre><code class=\"undefinedsh\">sudo apt-get remove etcher-electron\nsudo rm /etc/apt/sources.list.d/etcher.list\nsudo apt-get update\n</code></pre>\n<h4>Brew Cask (macOS)</h4>\n<p>Note that the Etcher Cask has to be updated manually to point to new versions,\nso it might not refer to the latest version immediately after an Etcher\nrelease.</p>\n<pre><code class=\"undefinedsh\">brew cask install etcher\n</code></pre>\n<h5>Uninstall</h5>\n<pre><code class=\"undefinedsh\">brew cask uninstall etcher\n</code></pre>\n"
